import React, { PureComponent, Fragment } from 'react';
import { connect } from "react-redux";
import { Table, Button, Popconfirm } from "antd";
import { actionCreators } from './store';
import {Link} from "react-router-dom";

class Channel extends PureComponent {
  render() {
    const { list, handleDeleteItem } = this.props;
    const colums = [
      {
        key: '_id',
        dataIndex: '_id',
        title: '_id',
        align: 'center'
      }, {
        key: 'title',
        dataIndex: 'title',
        title: '渠道名称',
        align: 'center'
      }, {
        key: 'name',
        dataIndex: 'name',
        title: '公号名称',
        align: 'center',
        render(item, record) {
          return <span>{record.official.name || ''}</span>
        }
      }, {
        key: 'image',
        title: '公号图片',
        align: 'center',
        render(item, record) {
          return <img style={{width: '120px', verticalAlign: 'middle'}} src={record.official.image || ''} alt=""/>
        }
      }, {
        key: 'action',
        title: 'Action',
        align: 'center',
        render(item, record, index) {
          let data = JSON.stringify(record);
          return (
            <Fragment>
              <Button style={{marginRight: '20px'}} size='small'>
                <Link to={'/build/channel/update?data=' + data}>基础配置</Link>
              </Button>
              <Button style={{marginRight: '20px'}} size='small' type='primary'>
                <Link to={'/build/channel/advertise?id=' + record._id + '&title=' + record.title}>广告配置</Link>
              </Button>
              <Popconfirm title="确定删除这条内容吗?" onConfirm={() => handleDeleteItem(list, record, index)} okText="Yes" cancelText="No">
                <Button size='small' type='danger'>删除</Button>
              </Popconfirm>
            </Fragment>
          )
        }
      },
    ];
    return (
      <Fragment>
        <Button style={{marginBottom: '30px'}} type='primary'><Link to='/build/channel/create'>添加渠道</Link></Button>
        <Table bordered rowKey={record => record._id} columns={colums} dataSource={list} pagination={false} />
      </Fragment>
    )
  }
  componentDidMount() {
    this.props.getChannelList()
  }
}

const mapState = (state) => ({
  list: state.get('channel').list
});

const mapDispatch = (dispatch) => ({
  getChannelList() {
    dispatch(actionCreators.getChannelList())
  },
  handleDeleteItem(list, record, index) {
    dispatch(actionCreators.handleDeleteItem(list, record, index))
  }
});

export default connect(mapState, mapDispatch)(Channel);