import React, {PureComponent} from 'react';
import { connect } from "react-redux";
import {Form, Input, Button, Upload, Icon} from "antd";
import { actionCreators } from '../store';
import { api } from '../../../api/config';

class CreateChannel extends PureComponent {
  render() {
    const { title, official, changeChannelInput, changeGonghaoInput, uploadImage, handleCreate, handleUpdate, location } = this.props;
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      }
    };
    const uploadProps = {
      action: api + '/distribution/upload',
      onChange: uploadImage,
      showUploadList: false,
      name: 'imageFile'
    };

    let submit = !location.search ? <Button style={{marginRight: '20px'}} type='primary' onClick={() => {handleCreate(this.props)}}>创建</Button> : <Button style={{marginRight: '20px'}} type='primary' onClick={() => {handleUpdate(this.props)}}>修改</Button>;

    return (
      <Form {...FormLabelCol}>
        <Form.Item label='渠道名称'>
          <Input value={title} onChange={changeChannelInput} placeholder='请输入渠道名称'/>
        </Form.Item>
        <Form.Item label='公号名称'>
          <Input value={official.name} onChange={changeGonghaoInput} placeholder='请输入公号名称'/>
        </Form.Item>
        <Form.Item label='公号图片'>
          <Upload {...uploadProps}>
            {official.image === ''
              ? (<Button><Icon type="upload" />上传图片</Button>)
              : (<img style={{width: '200px'}} src={official.image} alt=""/>)}
          </Upload>
        </Form.Item>
        <Form.Item style={{marginLeft: '100px'}}>
          {submit}
          <Button type='danger' onClick={() => this.props.history.goBack()}>取消</Button>
        </Form.Item>
      </Form>
    );
  }
  componentDidMount() {
    if(this.props.location.search) {
      let data = decodeURI(this.props.location.search);
      data = JSON.parse(data.split('=')[1]);
      this.props.initState(data)
    }
  }
}

const mapState = (state) => ({
  title: state.get('channel').title,
  official: state.get('channel').official,
  id: state.get('channel').id
});

const mapDispatch = (dispatch) => ({
  changeChannelInput(e) {
    let title = e.target.value;
    dispatch(actionCreators.changeChannelInput(title))
  },
  changeGonghaoInput(e) {
    let name = e.target.value;
    dispatch(actionCreators.changeGonghaoInput(name))
  },
  uploadImage(res) {
    if(res.file.status === 'done') {
      let image = api + '/distributions/' + res.file.response.image_url;
      dispatch(actionCreators.uploadImage(image))
    }
  },
  handleCreate(props) {
    dispatch(actionCreators.handleCreate(props))
  },
  initState(data) {
    dispatch(actionCreators.initState(data))
  },
  handleUpdate(props) {
    dispatch(actionCreators.handleUpdate(props))
  }
});

export default connect(mapState, mapDispatch)(CreateChannel);