import React, { PureComponent } from 'react';
import { connect } from 'react-redux';
import { Form, Input, Upload, Button, Icon } from 'antd';
import { actionCreators } from '../store';


class Modify extends PureComponent {
  render() {
    const { page_title, title, image_url, handleInputChange, handleChange, handleSubmit } = this.props;
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3},
      },
      wrapperCol: {
        sm: {span: 8},
      }
    };
    const uploadProps = {
      action: 'http://h.tyuss.com/book/upload',
      onChange: handleChange,
      showUploadList: false,
      name: 'imageFile'
    };
    return (
      <div className='modify'>
        <Form {...FormLabelCol}>
          <Form.Item label='小说名称'>
            <h3>{title}</h3>
          </Form.Item>
          <Form.Item label='页面标题'>
            <Input value={page_title} placeholder='请输入页面标题' onChange={handleInputChange}/>
          </Form.Item>
          <Form.Item label='banner图'>
            <Upload {...uploadProps}>
              {image_url === ''
                ? (<Button><Icon type="upload" />上传图片</Button>)
                : (<img style={{width: '200px'}} src={image_url} alt=""/>)}
            </Upload>
          </Form.Item>
          <Form.Item style={{marginLeft: '100px'}}>
            <Button style={{marginRight: '20px'}} type='primary' onClick={() => {handleSubmit(this.props)}}>提交</Button>
            <Button type='danger' onClick={() => this.props.history.goBack()}>取消</Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
  componentDidMount() {
    let record = decodeURI(this.props.location.search);
    record = JSON.parse(record.split('=')[1]);
    this.props.getNovelInfo(record);
  }
}

const mapState = (state) => ({
  page_title: state.get('novel').page_title,
  image_url: state.get('novel').image_url,
  title: state.get('novel').title,
  _id: state.get('novel')._id
});

const mapDispatch = (dispatch) => ({
  getNovelInfo(novelInfo) {
    dispatch(actionCreators.getNovelInfo(novelInfo))
  },
  handleInputChange(e) {
    let page_title = e.target.value;
    dispatch(actionCreators.handleInputChange(page_title))
  },
  handleChange(res) {
    if(res.file.status === 'done') {
      let image_url = 'http://h.tyuss.com/uploads/' + res.file.response.image_url
      dispatch(actionCreators.handleChange(image_url))
    }
  },
  handleSubmit(props) {
    dispatch(actionCreators.handleSubmit(props))
  }
});

export default connect(mapState, mapDispatch)(Modify);