import * as constants from './constants';

const defaultState = {
  list: [],
  page_title: '',
  image_url: '',
  title: '',
  _id: '',
  rules: []
};

export default (state = defaultState, action) => {
  if(action.type === constants.GET_NOVEL_LIST) {
    const newState = JSON.parse(JSON.stringify(state));
    newState.list = action.list;
    return newState;
  }
  if(action.type === constants.GET_NOVEL_INFO) {
    const newState = JSON.parse(JSON.stringify(state));
    newState.page_title = action.novelInfo.page_title;
    newState.image_url = action.novelInfo.image_url;
    newState.title = action.novelInfo.title;
    newState._id = action.novelInfo._id;
    return newState;
  }
  if(action.type === constants.CHANGE_INPUT) {
    const newState = JSON.parse(JSON.stringify(state));
    newState.page_title = action.page_title;
    return newState;
  }
  if(action.type === constants.UPLOAD_IMAGE) {
    const newState = JSON.parse(JSON.stringify(state));
    newState.image_url = action.image_url;
    return newState;
  }
  return state;
};