import React, { PureComponent, Fragment } from 'react';
import { connect } from "react-redux";
import { actionCreators } from '../store';
import { Form, Input, Table, Button } from "antd";

class Advertise extends PureComponent {
  render() {
    const { list, keys, platform, index, adkey, location, handleKeyChange, handlePlatformChange, handleAdkeyChange, addItemToList, saveUpdateItem, updateItem, deleteAdItem, submitAdList } = this.props;
    const title = decodeURI(location.search.split('=')[2]);
    const FormLabelCol = {
      labelCol: {
        sm: {span: 3}
      },
      wrapperCol: {
        sm: {span: 8}
      }
    };
    const columns = [
      {
        key: 'key',
        dataIndex: 'key',
        title: 'key',
        align: 'center'
      }, {
        key: 'platform',
        dataIndex: 'platform',
        title: '广告名称',
        align: 'center'
      }, {
        key: 'adkey',
        dataIndex: 'adkey',
        title: '广告名称',
        align: 'center'
      }, {
        key: 'action',
        title: 'Action',
        align: 'center',
        render(item, record, index) {
          return (
            <Fragment>
              <Button size='small' style={{marginRight: '20px'}} type='primary' onClick={() => {updateItem(record, index)}}>修改</Button>
              <Button size='small' type='danger' onClick={() => {deleteAdItem(index)}}>删除</Button>
            </Fragment>
          )
        }
      }
    ];
    let button;
    if(keys && platform && adkey && index !== null) {
      button = <Button style={{marginLeft: '130px'}} onClick={() => {saveUpdateItem(this.props)}}>保存</Button>
    } else {
      button = <Button style={{marginLeft: '130px'}} onClick={() => {addItemToList(this.props)}}>添加</Button>
    }
    return (
      <Fragment>
        <Form {...FormLabelCol}>
          <Form.Item label='渠道名称'>
          <h3>{title}</h3>
        </Form.Item>
          <Form.Item label='key'>
            <Input value={keys} placeholder='请输入key' onChange={handleKeyChange} />
          </Form.Item>
          <Form.Item label='广告平台'>
            <Input value={platform} placeholder='请输入广告平台' onChange={handlePlatformChange} />
          </Form.Item>
          <Form.Item label='adkey'>
            <Input value={adkey} placeholder='请输入adkey' onChange={handleAdkeyChange} />
          </Form.Item>
          <Form.Item>
            {button}
          </Form.Item>
        </Form>
        <Table bordered columns={columns} dataSource={list} pagination={false} />
        <div style={{margin: '30px 0 0 100px'}}>
          <Button style={{marginRight: '20px'}} type='primary' onClick={() => {submitAdList(this.props)}}>提交</Button>
          <Button type='danger' onClick={() => {this.props.history.goBack()}}>返回</Button>
        </div>
      </Fragment>
    )
  }
  componentDidMount() {
    this.props.getAdzones(this.props.location.search.split('=')[1].split('&')[0]);
  }
}

const mapState = (state) => ({
  list: [...state.get('channel').ad_list],
  keys: state.get('channel').keys,
  platform: state.get('channel').platform,
  adkey: state.get('channel').adkey,
  index: state.get('channel').index
});

const mapDispatch = (dispatch) => ({
  getAdzones(id) {
    dispatch(actionCreators.getAdzones(id))
  },
  handleKeyChange(e) {
    let key = e.target.value;
    dispatch(actionCreators.handleKeyChange(key))
  },
  handlePlatformChange(e) {
    let platform = e.target.value;
    dispatch(actionCreators.handlePlatformChange(platform))
  },
  handleAdkeyChange(e) {
    let adkey = e.target.value;
    dispatch(actionCreators.handleAdkeyChange(adkey))
  },
  addItemToList(props) {
    dispatch(actionCreators.addItemToList(props))
  },
  saveUpdateItem(props) {
    dispatch(actionCreators.saveUpdateItem(props))
  },
  updateItem(record, index) {
    dispatch(actionCreators.updateItem(record, index))
  },
  deleteAdItem(index) {
    dispatch(actionCreators.deleteAdItem(index))
  },
  submitAdList(props) {
    dispatch(actionCreators.submitAdList(props))
  }
});

export default connect(mapState, mapDispatch)(Advertise);